import React from 'react'

function Footer() {
  return (
    <div>
      <footer className="footer flex-col gap-0 z-100 justify-items-center bg-green-950  text-base-content p-10 ">
        <nav>
          <h6 className="footer-title">Services</h6>
          <a className="link link-hover">Branding</a>
          <a className="link link-hover">Design</a>
          <a className="link link-hover">Marketing</a>
          <a className="link link-hover">Advertisement</a>
        </nav>
        <nav>
          <h6 className="footer-title">Company</h6>
          <a className="link link-hover">About us</a>
          <a className="link link-hover">Contact</a>
          <a className="link link-hover">Jobs</a>
          <a className="link link-hover">Press kit</a>
        </nav>
        <nav>
          <h6 className="footer-title">Legal</h6>
          <a className="link link-hover">Terms of use</a>
          <a className="link link-hover">Privacy policy</a>
          <a className="link link-hover">Cookie policy</a>
        </nav>
        <form className='justify-items-center w-full'>
          <h6 className="footer-title">Newsletter</h6>
          <fieldset className="form-control w-80 pr-0">
            <label className="label ">
              <span className="label-text">Enter your email address</span>
            </label>
            <div className="join text-cenet">
              <input
                type="text"
                placeholder="username@site.com"
                className="input input-bordered join-item" />
              <button className="btn btn-primary join-item">Subscribe</button>
            </div>
          </fieldset>
        </form>
      </footer>
    </div>
  )
}

export default Footer
